<template>
  <div>
    <div v-show="displayVideo">
      <video controls id="video">
        <source src="./video/cestmonAnniversaire.mp4" type="video/mp4" />
        Your browser does not support HTML video.
      </video>

      <div class="centerDiv">
        <button class="button" @click="whoIsHere">Voir qui est la ?</button>
      </div>
    </div>
    <div v-show="displayAdd">
      <h1>
        Mon anniversaire se déroulera le 05 novembre 2022 à partir de 19h à la
        salle fêtes de Saulzoir (1 Rue Victor Hugo - 59227 Saulzoir)
      </h1>

      <p>
        Pour ceux qui doivent prendre le train on fera des voitures relais :) je
        vous conseil soit la gare de Cambrai ou de Valenciennes (Essayez
        d'arriver en même temps)
      </p>

      <p>
        Pourquoi les réseaux sociaux pour être en contact avec ceux que vous
        connaissez pas :)
      </p>

      <div class="centerDiv">
        <label for="firstName">Prénom :</label>
        <input
          type="text"
          placeholder="Ex: Baptiste"
          v-model="firstName"
          required
        />
        <label for="lastName">Nom :</label>
        <input
          type="text"
          placeholder="Ex: Hardelin"
          v-model="lastName"
          required
        />
        <label for="socialMdeia">Réseaux Social :</label>
        <input
          type="text"
          placeholder="Ex: (Discord) Baba_prog#5404, (Snap) baba_prog"
          v-model="socialMedia"
          required
        />

        <button @click="addGuest">Je suis la !</button>
      </div>
    </div>

    <div v-if="displayGuest">
      <div v-if="displayGuest" class="playsound">{{ playSound }}</div>
      <table>
        <tr>
          <th>Prénom</th>
          <th>Nom</th>
          <th>Réseaux sociaux</th>
        </tr>
        <tr
          v-for="(guest, index) in guests"
          v-bind:key="guest._id"
          v-bind:item="guest"
          v-bind:index="index"
        >
          <th>{{ guest.firstName }}</th>
          <th>{{ guest.lastName }}</th>
          <th>{{ guest.socialMedia }}</th>
        </tr>
      </table>

      <audio controls autoplay="autoplay" id="audio" class="playsound">
        <source src="./asterix.mp3" type="audio/mpeg" />
        No audio support.
      </audio>

      <div class="centerDiv">
        <button class="button" @click="backToHome">Revenir au début</button>
      </div>
    </div>
  </div>
</template>

<script>
import GuestsService from "./GuestsService";

export default {
  name: "App",
  components: {},
  data() {
    return {
      displayVideo: true,
      displayAdd: false,
      displayGuest: false,
      play: true,
      isLoad: false,
      firstName: "",
      lastName: "",
      socialMedia: "",
      guests: [],
      error: "",
      autoplay: false,
    };
  },
  created() {
    document.addEventListener("DOMContentLoaded", () => {
      const video = document.getElementById("video");
      video.currentTime = 0;
      video.addEventListener("ended", () => {
        this.displayVideo = false;
        this.displayAdd = true;
      });
    });
  },
  methods: {
    async addGuest() {
      await GuestsService.createGuest(
        this.firstName,
        this.lastName,
        this.socialMedia
      );
      this.displayAdd = false;
      this.displayGuest = true;

      await this.displayGuests();
    },
    async displayGuests() {
      try {
        this.guests = await GuestsService.getGuest();
      } catch (error) {
        this.error = error.message;
      }
    },
    playSound() {
      const play = new Audio("./asterix.mp3");

      if (play !== null) {
        play.currentTime = 0;
        play.play();
      }
    },
    async whoIsHere() {
      this.displayVideo = false;
      this.displayGuest = true;
      await this.displayGuests();
    },

    backToHome() {
      this.displayGuest = false;
      this.displayVideo = true;
    },
  },
};
</script>

