import axios from "axios";

const url = "/api/guests";

class GuestsService {
  static getGuest() {
    // eslint-disable-next-line no-async-promise-executor
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(url);
        const data = res.data;
        resolve(
          data.map((guest) => ({
            ...guest,
          }))
        );
      } catch (error) {
        reject(error);
      }
    });
  }

  static createGuest(firstName, lastName, socialMedia) {
    return axios.post(url, { firstName, lastName, socialMedia });
  }

  static deletePost(id) {
    return axios.delete(url + id, {
      id,
    });
  }
}

export default GuestsService;
